/**
 * config data 说明
 * config data 是一些组件/控件的配置项
 * 配置项内容不定
 * 目前常见配置如下，具体特殊配置 需看移动端需求
 * title： 标题
 * type： 类型
 * isHide：是否隐藏该组件/控件
 * pageIndex: 组件所属页数
 * disabled: 是否禁用
 * maxlength：值最大长度
 * option/options: 选项内容
 * showLogicList: 显示逻辑
 */

