
import { Vue, Component, Prop, Ref } from 'vue-property-decorator'
import { cloneDeep } from 'lodash'
import { v4 } from 'uuid'

import Sortable from 'sortablejs'

@Component({
  name: 'elementGroup'
})
export default class elementGroup extends Vue {
  @Ref('elementGroup') elementGroupRef!: HTMLElement

  @Prop({ type: String }) title!: string
  @Prop({ required: true, type: Array }) list!: Array<pageComponentsInterface | pageWidgetsInterface>
  @Prop({ required: true, type: String }) groupName!: 'components' | 'widgets'
  @Prop({ type: Number, default: 1 }) pageIndex!: number

  errorTime!: number

  // 创建 sortable
  createdSortable() {
    if (!this.elementGroupRef) {
      return
    }

    new Sortable(this.elementGroupRef, {
      sort: false,

      group: {
        name: this.groupName,
        pull: 'clone',
        put: false
      },

      onStart: (evtData: any) => {
        const index = evtData.oldIndex || 0

        evtData.item._underlying_vm_ = this.cloneData(this.list[index])
      },

      onEnd: (evtData: any) => {
        const { oldIndex, originalEvent, to } = evtData

        // 判断元素是否拖动到手机预览页面内了，如果没有，就不提示，直接返回
        if (window === originalEvent.view) return

        const item = this.list[oldIndex || 0] as pageWidgetsInterface

        const { depComponentType, depComponentName, depComponentClassOnPreview } = item

        if (depComponentClassOnPreview && depComponentClassOnPreview !== to.className) {
          this.$message.warning(`请将当前控件放置在${depComponentName}中`)

          this.showErrorTipsByComponent(depComponentType)
        }
      }
    })
  }

  cloneData(data: pageComponentsInterface | pageWidgetsInterface) {
    let _data!: pageComponentsInterface | pageWidgetsInterface

    if (this.groupName === 'widgets') {
      _data = cloneDeep(data) as pageWidgetsInterface
      _data.field = `${_data.field}_${v4()}`
    } else {
      _data = cloneDeep(data) as pageComponentsInterface
      _data.key = `${_data.key}_${v4()}`
    }

    _data.configData.pageIndex = this.pageIndex

    return _data
  }

  // 显示 警示
  showErrorTipsByComponent(componentType: string) {
    const els = document.querySelectorAll('.element-group__list .element-group__item')

    const errorTipsClass = 'error-tips'
    const componentItemClass = `group-item__${componentType}`

    // 记录那些元素 被标红 3秒后取消标红
    const errorEls: Array<Element> = []

    clearTimeout(this.errorTime)

    els.forEach((el) => {
      if (el.className.includes(componentItemClass)) {
        el.classList.add(errorTipsClass)
        errorEls.push(el)
      } else {
        el.classList.remove(errorTipsClass)
      }
    })

    // 滚动到第一个错误页
    ;(() => {
      const el = errorEls[0]
      if (el) el.scrollIntoView({ block: 'center', behavior: 'smooth' })
    })()

    this.errorTime = setTimeout(() => {
      errorEls.forEach((el) => el.classList.remove(errorTipsClass))
    }, 3000)
  }

  mounted() {
    this.createdSortable()
  }
}
